<template>
  <v-card class="mx-3 mt-4">
    <v-card-text>
      <v-row>
        <v-col class="d-flex justify-center">
          <v-btn icon>
            <v-avatar size="60">
              <img :src="uavatar" alt />
            </v-avatar>
          </v-btn>
        </v-col>
      </v-row>
      <v-row>
        <v-col
          class="d-flex justify-center"
          style="font-size: 12px;color: #999;padding-top:20px;"
        >by {{ uname }}</v-col>
      </v-row>

      <v-row>
        <v-col class="d-flex justify-center">
          <span class="title">{{title}}</span>
        </v-col>
      </v-row>
      <v-divider></v-divider>
      <v-row>
        <v-col cols="6">
          <v-icon size="28" class="mr-1">mdi-calendar</v-icon><span class="subtitle-2">{{date}}</span>
        </v-col>
        <v-col cols="6">
          <v-icon size="28" class="mr-1">mdi-earth</v-icon><span class="subtitle-2">{{location}}</span>
        </v-col>
      </v-row>
      <div class="photo-ctn">
        <v-img
          src="http://photos.breadtrip.com/photo_2019_10_13_0eac9e3e0592219ae5c8cf068b63078b.jpg?imageView/2/w/640/q/85"
        ></v-img>
        <div class="wp-btns">
          <a class="comment-btn">
            <i class="icon-comment"></i>
            <span>0</span>
          </a>
          <i class="icon-btnbg"></i>
          <a class="like-btn">
            <i class="icon-like"></i>
            <span>3</span>
          </a>
        </div>
      </div>

      <v-card-text>
        <div class="text--primary">{{content}}</div>
      </v-card-text>

      <v-row v-show="uid===this.$store.state.user.userinfo.id">
        <v-col>
          <v-btn icon @click="editcard">
            <v-icon>mdi-content-save-edit</v-icon>
          </v-btn>
          <v-btn icon @click="delcard">
            <v-icon>mdi-delete</v-icon>
          </v-btn>
        </v-col>
      </v-row>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    uid: null,
    uavatar: null,
    uname: "",

    id: "",
    title: "",
    pic: null,
    content: "",
    location: "",
    date: new Date().toISOString().substr(0, 10),
    menu: false,

    likeUsers: [], //点赞用户列表
    comments: [], //评论列表
    commentId: undefined //评论 ID
  }),
  created() {
    const cardId = this.$route.params.cardId;
    this.axios
      .get(`/card/${cardId}/`)
      .then(response => {
        let obj = response.data;
        this.uid = obj.userprofile.id;
        this.uavatar = obj.userprofile.avatar;
        this.uname = obj.userprofile.username;
        this.id = obj.id;
        this.title = obj.title;
        this.pic = obj.pic;
        this.content = obj.content;
        this.location = obj.location;
        this.date = obj.date;
      })
      .catch(error => {
        this.$store.dispatch("updateAlter", {
          msg: "查看失败",
          msgType: "error",
          msgShow: true
        });
        this.msgtimer = setTimeout(() => {
          this.$store.dispatch("updateAlter", { msgShow: false });
        }, 3300);
        this.$router.back(-1);
      });
  },
  methods: {
    editcard() {
      this.$router.push(`/cards/${this.id}/edit`);
    },
    delcard() {
      const cardId = this.id;
      const headers = {
        Authorization: `jwt ${this.$store.state.user.token}`
      };
      this.axios
        .delete(`/card/${cardId}/`, { headers: headers })
        .then(response => {
          this.$store.dispatch("updateAlter", {
            msg: response.data.msg,
            msgType: "success",
            msgShow: true
          });
          this.msgtimer = setTimeout(() => {
            this.$store.dispatch("updateAlter", { msgShow: false });
          }, 3300);
          this.$router.push({
            name: "Home"
          });
        })
        .catch(error => {
          this.$store.dispatch("updateAlter", {
            msg: "网络异常, 查看失败",
            msgType: "error",
            msgShow: true
          });
          this.msgtimer = setTimeout(() => {
            this.$store.dispatch("updateAlter", { msgShow: false });
          }, 3300);
        });
    }
  }
};
</script>

